    -moz-box-sizing:border-box;
    box-sizing:border-box;
    margin:0;	
  }
  body {
    background:none repeat scroll 0 0 #eeeeee;
    font-size:16px;
    font-family:"Open Sans",Arial,sans-serif;
    color:#444;
  }
  h1,h2,h3,h4,h5,h6 {
    color:#152732;
    font-weight: normal;
    line-height: 1.3;
